<template>
  <div style="position:relative;">
    <HeaderTop :showBack="showBack" :showTtl="showTtl" :showU="showU"></HeaderTop>
    <div class="g-signUp">
      <div class="m-ipt">
        <input type="text" v-model="phoneNum" placeholder="">
      </div>
      <div class="m-ipt">
        <input type="text" v-model="validateCode" class="ipt-width" placeholder="">
        <div class="send-code" @click="getCode"></div>
      </div>

      <div class="m-sigle-btn">
        <!-- <router-link to="/sign/complete"> -->
        <mt-button type="default" size="small" @click="goToNext"></mt-button>
        <!-- </router-link> -->
      </div>
    </div>
  </div>
</template>

<script>
import HeaderTop from '@/components/HeaderTop'
import { getSendSMS } from '@/api/lession'
export default {
  name: "signup",
  components: {
    HeaderTop
  },
  data() {
    return {
      showU: false,
      showBack: true,
      showTtl: '',
      phoneNum: null,
      validateCode: null,
    }
  },
  mounted() {},
  methods: {
    // 
    async getCode() {
      let num = this.phoneNum
      let testPhone = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/i;
      if(!num) {
        alert('')
        return false
      }

      console.log(testPhone.test(num))
      if(!testPhone.test(num)) {
        alert('')
      }

      let ret = await getSendSMS({
        phone: num
      })
    },

    // 
    goToNext() {
      let num = this.phoneNum, code = this.validateCode;
      let testPhone = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/i, testcode = /^[0-9]{6}$/i;
      if(!num) {
        alert('')
        return false
      }
      if(!testPhone.test(num)) {
        alert('')
      }
      if(!code) {
        alert('')
        return false
      }
      if(!testcode.test(code)) {
         alert('')
        return false
      }
      this.$router.push({
        path: "complete"
      })
    }
  }
};
</script>



